import {FETCHED_STORIES, FETCHED_USER_STORIES} from './actionType';
import {fetchedStories} from '../../utils/api/vk';

export const fetcheStories = (extended) => async (dispatch) => {
	const stories = await fetchedStories({extended});
	dispatch({
		type: FETCHED_STORIES,
		items: [...stories.items],
		profiles: [...stories.profiles]
	});
};

export const fetcheUserStories = (id) => async (dispatch) => {
	dispatch({type: FETCHED_USER_STORIES, id});
};