import {FETCHED_STORIES, FETCHED_USER_STORIES} from './actionType';

const initialState = {
	items: [],
	profiles: [],
	userStoreis: []
};

export default function stories (state=initialState, action={}) {
	switch(action.type) {
		case FETCHED_STORIES:
			return {
				...state,
				items: [...action.items],
				profiles: [...action.profiles]
			};
		case FETCHED_USER_STORIES:
			return {
				...state,
				userStoreis: state.profiles.filter(({id}) => id === action.id)
			}
		default:
			return state;
	}
}

export const getStories = (state) => {
	return state.stories.items;
};

export const getProfiles = (state) => {
	return state.stories.profiles;
};

export const userStoreis = (state) => {
	return state.stories.userStoreis;
};