import React, {useEffect} from 'react';
import PropTypes from 'prop-types';
import {connect} from 'react-redux';

import {selectStories} from '../../store/reducers';
import {storiesActions} from '../../store/actions';

import {Panel, List, Cell, Avatar} from '@vkontakte/vkui';
import Icon24MoreHorizontal from '@vkontakte/icons/dist/24/back';

import PanelHeader from '../../components/HeaderPanel';
import Story from '../../components/Story/Story';

import'./Stories.css';

const UserStory = ({id, go, dispatch, userStoryId, userStoreis}) => {
	const {fetcheUserStories} = storiesActions;
	useEffect(() => {
		dispatch(fetcheUserStories(userStoryId));
	}, []);

	console.log(userStoreis);

	const handleClick = () => {

	};

	return(
		<Panel id={id}>
			<PanelHeader handelClick={go} dataTo='stories' title='Истории Пользователя' />
			<div className='Stories__wrapper'>
				<Story handleClick={handleClick}
						id={121112} name='sfsdffsdf'/>
			</div>
		</Panel>
	);
}

UserStory.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired
};

const mapStateToProps = (state) => ({
	userStoreis: selectStories.userStoreis(state)
});

export default connect(mapStateToProps, null)(UserStory);