import React, {useEffect} from 'react';
import PropTypes from 'prop-types';
import {connect} from 'react-redux';

import {selectStories, selectUser} from '../../store/reducers';
import {storiesActions, userActions} from '../../store/actions';

import {Panel} from '@vkontakte/vkui';

import PanelHeader from '../../components/HeaderPanel';
import Story from '../../components/Story/Story';

import'./Stories.css';

const Stories = ({id, go, setUserStoryId, dispatch, profiles, usersProfile}) => {
	const {fetcheStories} = storiesActions;
	const {fetchedUsersProfile} = userActions;

	useEffect(() => {
		dispatch(fetcheStories({extended: 1}));
	}, []);

	if (profiles.length > 0 && !usersProfile.length) {
		const userIds = profiles.map(({id}) => id);
		dispatch(fetchedUsersProfile({user_ids: userIds}));
	}

	const handleClick = (id) => {
		setUserStoryId({ id, to:'userStory' });
	}

	return(
		<Panel id={id}>
			<PanelHeader handelClick={go} dataTo='home' title='Истории' />
			<div className='Stories__wrapper'>
				{usersProfile.length > 0 && usersProfile.map(({id, first_name, last_name, photo_100}) =>
					<Story
						handleClick={handleClick}
						id={id} name={`${first_name} ${last_name}`} 
						photo={photo_100}/>)}

				<Story handleClick={handleClick}
						id={121112} name='sfsdffsdf'/>
			</div>
		</Panel>
	);
}

Stories.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired
};

const mapStateToProps = (state) => ({
	profiles: selectStories.getProfiles(state),
	usersProfile: selectUser.getUsersProfile(state)
});

export default connect(mapStateToProps, null)(Stories);